.section {
  background-image: url(../../../assets/destination/background-destination-desktop.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  z-index: -1;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: white;
  overflow: hidden;
  background-color: #0b0d17;
}

.info {
  display: flex;
  flex-direction: row;
  margin-top: 76px;
}

.title {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 508px;
  height: 576px;
  margin-right: 314px;
}

.title h5 {
  font-family: Barlow Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: 4.725px;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0;
}

.title h5 b {
  mix-blend-mode: normal;
  opacity: 0.25;
  margin-right: 28px;
}

.title img {
  margin-top: 130px;
  margin-left: 150px;
}

.details {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  width: 445px;
  height: 472px;
  margin-top: 110px;
}

.ul {
  list-style: none;
  display: flex;
  justify-content: flex-start;
  font-family: Barlow Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  color: #d0d6f9;
  text-transform: uppercase;
  width: 100%;
}

.ul li {
  cursor: pointer;
  margin-right: 36px;
  padding-bottom: 12px;
}

.active {
  border-bottom: 3px solid white !important;
}

.ul li:hover {
  border-bottom: 3px solid rgba(255, 255, 255, 0.5);
}

.description h2 {
  font-family: Bellefair;
  font-style: normal;
  font-weight: normal;
  font-size: 100px;
  line-height: 115px;
  color: #ffffff;
  text-transform: uppercase;
  margin: 25px 0;
}

.description span {
  font-family: Barlow;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 32px;
  color: #d0d6f9;
}

.metrics {
  display: flex;
  width: 100%;
  justify-content: flex-start;
  margin-top: 86px;
}

.metrics div {
  margin-right: 79px;
}

.metrics h6 {
  font-family: Barlow Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 2.3625px;
  text-transform: uppercase;
  color: #d0d6f9;
  margin: 0;
}

.metrics p {
  font-family: Bellefair;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 32px;
  text-transform: uppercase;
  color: #ffffff;
  margin-top: 12px;
}

@media screen and (max-width: 1024px) {
  .section {
    background-image: url(../../../assets/destination/background-destination-tablet.jpg);
  }

  .info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: auto;
  }

  .title {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;
  }
  .title h5 {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 3.375px;
    align-self: flex-start;
    margin-left: 38px;
    margin-top: 18%;
  }

  .title img {
    width: 300px;
    height: 300px;
    margin: 60px 0px 53px 0px;
  }

  .ul {
    justify-content: center;
    align-items: center;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
  }

  .ul li {
    margin: 0px 18px;
  }

  .details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    text-align: center;
    margin: auto;
  }

  .description h2 {
    font-size: 80px;
    line-height: 92px;
    margin: 25px 0;
  }

  .description span {
    width: 100%;
    height: 30%;
  }

  .metrics {
    align-items: center;
    justify-content: center;
    margin: 45px 0px 50px 0px;
    border-top: 1px solid #383b4b;
    padding-top: 29px;
  }

  .metrics div {
    margin: 0px 40px;
  }
  .metrics h6 {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 2.3625px;
  }

  .metrics p {
    font-size: 28px;
    line-height: 32px;
  }
}

@media screen and (max-width: 375px) {
  .section {
    justify-content: center;
    align-items: center;
    overflow-y: scroll;
  }

  .info {
    width: 100%;
  }

  .title {
    align-items: center;
    height: auto;
    justify-content: flex-start;
    margin-bottom: 26px;
  }

  .title h5 {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
    align-self: center;
    margin-left: 0px;
    margin-top: 25%;
  }

  .title img {
    width: 170px;
    max-height: 170px;
    margin: 26px 102px;
  }

  .details {
    justify-content: unset;
    align-items: center;
    text-align: center;
    margin: auto;
    width: 87%;
  }

  .ul {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 2.3625px;
  }

  .ul li {
    margin: 0px 13px;
  }

  .description {
    min-height: 50%;
  }

  .description h2 {
    font-size: 56px;
    line-height: 64px;
    margin: 20px 0 0 0;
  }

  .description span {
    width: 90%;
    font-size: 15px;
    line-height: 25px;
  }

  .metrics {
    align-items: center;
    justify-content: center;
    margin: 0px 0px 50px 0px;
    border-top: 1px solid #383b4b;
    padding-top: 32px;
    width: 100%;
    flex-direction: column;
  }

  .metrics div {
    margin-bottom: 32px;
  }
  .metrics h6 {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 2.3625px;
  }

  .metrics p {
    font-size: 28px;
    line-height: 32px;
  }
}
