.section {
  background-image: url(../../../assets/crew/background-crew-desktop.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  z-index: -1;
  color: white;
  overflow: hidden;
  background-color: #0b0d17;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0 165px;
}

.info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}
.details {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 508px;
  height: 576px;
  margin-top: 20%;
}

.details h5 {
  font-family: Barlow Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: 4.725px;
  text-transform: uppercase;
  margin: 0;
}

.details h5 b {
  mix-blend-mode: normal;
  opacity: 0.25;
  margin-right: 24px;
}

.details div h4 {
  font-family: Bellefair;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 37px;
  text-transform: uppercase;
  mix-blend-mode: normal;
  opacity: 0.5;
  margin-top: 140px;
  margin-bottom: 0;
}

.details div h3 {
  font-family: Bellefair;
  font-style: normal;
  font-weight: normal;
  font-size: 56px;
  line-height: 64px;
  text-transform: uppercase;
  margin: 15px 0px 35px;
}

.details div span {
  font-family: Barlow;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 32px;
  text-align: justify;
  color: #d0d6f9;
}
.ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  position: absolute;
  bottom: 5%;
}

.ul li {
  cursor: pointer;
  width: 15px;
  height: 15px;
  background: rgba(255, 255, 255, 0.17);
  border-radius: 50%;
  margin-right: 24px;
}

.ul li:hover {
  background: rgba(255, 255, 255, 0.5);
}

.active {
  background: white !important;
}

.info img {
  align-self: flex-end;
}

@media screen and (max-width: 1024px) {
  .section {
    background-image: url(../../../assets/crew/background-crew-tablet.jpg);
  }
  .info {
    display: flex;
    flex-direction: column;
    justify-content: sp;
    align-items: center;
    height: 100vh;
    margin-top: 10%;
  }
  .details {
    align-items: center;
    width: 90vw;
    margin-top: 20%;
    text-align: center;
  }
  .details h5 {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 3.375px;
    margin: 0;
    align-self: flex-start;
  }

  .details div {
    width: 60vw;
  }

  .details div h4 {
    font-size: 24px;
    line-height: 28px;
    margin-top: 50px;
    margin-bottom: 0;
  }

  .details div h3 {
    font-size: 40px;
    line-height: 46px;
    margin: 15px 0px 35px;
  }

  .details div span {
    font-size: 16px;
    line-height: 28px;
  }

  .ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    position: absolute;
    top: 45%;
  }

  .info img {
    align-self: center;
    justify-self: center;
    max-width: 100%;
    max-height: 50vh;
  }
}

@media screen and (max-width: 375px) {
  .section {
    width: 100vw;
    padding: 0;
    overflow-y: scroll;
  }

  .info {
    width: 100%;
    flex-direction: column-reverse;
    align-items: center;
    margin-top: 0;
  }

  .info img {
    align-self: center;
    justify-self: center;
    max-height: 35vh;
    margin-top: 130px;
    border-bottom: 1px solid #383b4b;
  }

  .ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    position: absolute;
    top: 59%;
  }

  .ul li {
    cursor: pointer;
    width: 10px;
    height: 10px;
    margin: 0px 8px;
  }

  .details {
    align-items: center;
    width: 90vw;
    margin-top: 74px;
    text-align: center;
  }

  .details div {
    width: 100%;
  }
  .details h5 {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
    align-self: center;
    position: absolute;
    top: 12%;
  }

  .details div h4 {
    font-size: 16px;
    line-height: 18px;
    margin-top: 0px;
    margin-bottom: 0;
  }

  .details div h3 {
    font-size: 24px;
    line-height: 28px;
    margin: 15px 0px;
  }

  .details div span {
    font-size: 15px;
    line-height: 25px;
    padding-bottom: 50px;
  }
}
