.section {
  background-image: url(../../../assets/technology/background-technology-desktop.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  z-index: -1;
  color: white;
  overflow: hidden;
  background-color: #0b0d17;
}

.info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 802px;
  margin-top: 212px;
}

.details {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 165px;
}

.details h5 {
  font-family: Barlow Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: 4.725px;
  text-transform: uppercase;
  margin: 0;
}

.details h5 b {
  margin-right: 24px;
  mix-blend-mode: normal;
  opacity: 0.25;
}

.description {
  display: flex;
  flex-direction: row;
  width: 630px;
  height: 304px;
  margin-top: 137px;
  margin-right: 130px;
}

.description ul {
  list-style: none;
  padding: 0px;
  margin: 0px 80px 0px 0px;
}

.description ul li {
  cursor: pointer;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 32px;
  border: 1px solid rgba(255, 255, 255, 0.25);
}

.description ul li:hover {
  border: 1px solid white;
}

.active {
  background-color: white;
  color: #0b0d17;
}
.description ul li span {
  font-family: Bellefair;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 37px;
  text-align: center;
  letter-spacing: 2px;
}

.detailedText p {
  font-family: Barlow Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  color: #d0d6f9;
  text-transform: uppercase;
  margin-top: 0px;
}

.detailedText h3 {
  font-family: Bellefair;
  font-style: normal;
  font-weight: normal;
  font-size: 56px;
  line-height: 64px;
  text-transform: uppercase;
  margin: 16px 0px 17px;
}

.detailedText span {
  font-family: Barlow;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 32px;
  color: #d0d6f9;
}

.image {
  width: 515px;
  height: 527px;
  margin-top: 60px;
}

.image img {
  object-fit: fill;
  max-width: unset;
}

@media screen and (max-width: 1024px) {
  .section {
    background-image: url(../../../assets/technology/background-technology-tablet.jpg);
  }

  .info {
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-evenly;
    align-items: center;
    width: 100vw;
    height: 70vh;
  }

  .details h5 {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 3.375px;
    margin: 0;
    position: absolute;
    top: 14%;
    left: 5%;
  }
  .description {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 56px;
    text-align: center;
    height: auto;
    width: 70%;
  }

  .description ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    padding: 0px;
    margin: 10px 0px 44px 0px;
  }

  .description ul li {
    width: 60px;
    height: 60px;
    margin: 0px 8px;
  }
  .description ul li span {
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 1.5px;
  }

  .details {
    margin: 0px;
    align-items: center;
  }

  .detailedText p {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
  }

  .detailedText h3 {
    font-size: 40px;
    line-height: 46px;
  }

  .detailedText span {
    font-size: 16px;
    line-height: 28px;
  }

  .image {
    width: 100vw;
    height: auto;
    margin-top: 126px;
  }
  .image img {
    object-fit: cover;
    width: 100%;
  }
}

@media screen and (max-width: 375px) {
  .section {
    width: 100vw;
    overflow-y: scroll;
  }

  .info {
    width: 100%;
    height: unset;
    margin-top: 32px;
  }
  .details h5 {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
    left: unset;
  }

  .description {
    margin: 15px 0px 26px 0px;
    text-align: center;
    height: auto;
    width: 90%;
  }

  .description ul {
    margin: 20px 0px 26px 0px;
  }

  .description ul li {
    width: 40px;
    height: 40px;
  }

  .description ul li span {
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 1px;
  }

  .detailedText p {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 2.3625px;
  }

  .detailedText h3 {
    font-size: 24px;
    line-height: 28px;
  }

  .detailedText span {
    font-size: 15px;
    line-height: 25px;
    padding-bottom: 81px;
  }

  .image {
    width: 100vw;
    margin-top: 120px;
  }

  .image img {
    width: 100%;
    object-fit: fill;
    height: 170px;
  }
}
