.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 96px;
  width: 100%;
  align-items: center;
  position: absolute;
  top: 40px;
  left: 0;
}

.logo {
  margin-left: 55px;
}

.line {
  width: 30%;
  height: 1px;
  background: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.25;
  position: relative;
  left: 5%;
  z-index: 1;
}

.hamburger {
  display: none;
}

.nav {
  background: rgba(255, 255, 255, 0.04);
  backdrop-filter: blur(81.5485px);
  height: 100%;
  display: flex;
  align-items: center;
  width: 60vw;
}

.ul {
  height: 100%;
  width: 830px;
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 123px;
  margin-right: 165px;
}

.ul li {
  border-bottom: 3px solid rgba(255, 255, 255, 0);
  display: flex;
  align-items: center;
  height: 100%;
  margin-right: 50px;
  font-family: Barlow Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  text-decoration: none;
}

.ul li:hover {
  border-bottom: 3px solid rgba(255, 255, 255, 0.5);
}

.ul a {
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
}

.active {
  border-bottom: 3px solid white !important;
}

@media screen and (max-width: 1024px) {
  .header {
    top: 0;
  }
  .section {
    justify-content: center;
  }
  .logo {
    margin-left: 39px;
  }

  .line {
    display: none;
  }
  .nav {
    width: 60%;
    margin-right: 0px;
  }

  .ul {
    margin: 0px 46px;
  }

  .ul li {
    margin-right: 42px;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 2.3625px;
  }

  .ul li b {
    display: none;
  }
}

@media screen and (max-width: 375px) {
  .header {
    width: 100vw;
    top: 0;
  }

  .logo {
    margin-left: 24px;
  }

  .logo img {
    width: 40px;
    height: 40px;
  }

  .line {
    display: none;
  }

  .nav {
    display: none;
  }

  .hamburger {
    display: block;
    margin-right: 24px;
    cursor: pointer;
    padding: 10px;
    z-index: 2;
  }

  .open {
    display: flex;
    position: absolute;
    height: 100vh;
    top: 0;
    right: 0;
    align-items: flex-start;
    padding-top: 118px;
    padding-left: 32px;
  }

  .open ul {
    display: flex;
    flex-direction: column;
    width: unset;
    margin: 0;
    height: auto;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 0;
  }

  .open ul li {
    margin-bottom: 32px;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
  }

  .active {
    border-bottom: 3px solid rgba(255, 255, 255, 0) !important;
  }

  .ul li:hover {
    border-bottom: 3px solid rgba(255, 255, 255, 0);
  }
}
