.section {
  background-image: url(../../../assets/home/background-home-desktop.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  z-index: -1;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: #0b0d17;
}

.info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 250px;
}

.infoText {
  color: white;
  text-align: justify;
  width: 450px;
  height: 382px;
  margin-right: 300px;
}

.infoText h5 {
  font-family: "Barlow Condensed", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: 4.725px;
  text-transform: uppercase;
  color: #d0d6f9;
  margin-bottom: 24px;
  margin-top: 0px;
}

.infoText h1 {
  font-family: "Bellefair", serif;
  font-style: normal;
  font-weight: normal;
  font-size: 150px;
  line-height: 172px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 35px;
}

.infoText span {
  font-family: "Barlow", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 32px;
  color: #d0d6f9;
}

.explore {
  margin-left: 300px;
  width: 274px;
  height: 274px;
  border-radius: 50%;
  border: none;
  transition: 0.3s;
}

.explore:hover {
  box-shadow: 0px 0px 0px 88px rgba(255, 255, 255, 0.1);
}

.explore h4 {
  font-family: "Bellefair", serif;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 37px;
  letter-spacing: 2px;
}

.explore a {
  text-decoration: none;
  color: #0b0d17;
}

@media screen and (max-width: 1024px) {
  .section {
    background-image: url(../../../assets/home/background-home-tablet.jpg);
    overflow: hidden;
    background-size: cover;
    margin: auto;
  }

  .info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
  }

  .infoText {
    width: 55%;
    margin-top: 20%;
    margin-right: 0px;
    text-align: center;
  }

  .infoText h5 {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 3.375px;
  }

  .infoText h1 {
    font-size: 150px;
    line-height: 150px;
  }

  .infoText span {
    font-size: 16px;
    line-height: 28px;
  }

  .explore {
    margin-top: 10%;
    margin-left: 0px;
    width: 242px;
    height: 242px;
  }
}

@media screen and (max-width: 375px) {
  .infoText {
    width: 85%;
    margin-top: 20%;
    margin-right: 0px;
    text-align: center;
  }

  .infoText h5 {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
  }

  .infoText h1 {
    font-size: 80px;
    line-height: 100px;
    margin: 16px 0px;
  }

  .infoText span {
    font-size: 15px;
    line-height: 25px;
  }

  .explore {
    margin: 0px;
    width: 150px;
    height: 150px;
  }

  .explore h4 {
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    letter-spacing: 1.25px;
  }
}
